/* eslint-disable */
import axios from 'axios'
import {isWechat} from '../../lib/util'
import {Wechat} from '../../config/urls'

/**
 * 初始化微信sdk
 * */
export const initWechatSDK = function (url) {
  // if (!isWechat()) return
  let script = document.createElement('script')
  script.type = 'text/javascript'
  script.setAttribute('src', 'https://res.wx.qq.com/open/js/jweixin-1.2.0.js')
  document.head.appendChild(script)
  axios.get(Wechat.getWechat + encodeURIComponent(url || window.location.href))
    .then(data => {
      let datas = data.data
      wx.config({
        appId: datas.appId,
        timestamp: datas.timestamp,
        signature: datas.signature,
        nonceStr: datas.nonceStr,
        // debug: true,
        jsApiList: ['chooseWXPay']
      })
      wx.ready(() => {
        // alert('微信准备就绪')
      })
    })
}

export const wechatLogin = function () {
  // if (!isWechat()) return
  let url = Wechat.login + '/wechat/redirect?url=' + window.location.pathname + window.location.hash
  axios.get(url)
    .then(data => {
      window.location.href = data.data.data.url
    })
}



