import * as types from '../mutation-types'
import {getLocalStorage, setLocalStorage} from '../../lib/util'

const state = {
  labels: getLocalStorage('labels') || ''
}

const getters = {
  getLabels: state => state.labels
}

const mutations = {
  [types.SET_FORUM_LABEL] (state, labels) {
    state.labels = labels
    setLocalStorage('labels', JSON.stringify(labels))
  }
}

export default {
  state,
  getters,
  mutations
}



