import axios from 'axios'
import {MaintainUrl, TrackUrl} from '../config/urls'

export const getTrackReport = (ID) => {
  return new Promise((resolve, reject) => {
    axios.post(TrackUrl.trackGetReport, {vhcle: ID})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

export const getTrackDatas = (vhcle) => {
  return new Promise((resolve, reject) => {
    axios.post(TrackUrl.trackGetDatas, {vhcle: vhcle})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * 获取车辆的行车轨迹
 * @param {int} vhcle 车辆的编号
 * @param {int} start 开始时间的时间戳
 * @param {int} end 结束时间的时间戳
 * @return {Promise} 获取到的数据
 * */
export const getTrackInfos = (vhcle, start, end) => {
  return new Promise((resolve, reject) => {
    axios.post(TrackUrl.getTrackInfo, {vhcle: vhcle, start: start, end: end})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

// 获取油耗对比
export const getFuel = (vinNun) => {
  return new Promise((resolve, reject) => {
    axios.post(MaintainUrl.contrast, {vin: vinNun})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

