/**
 * @param {string} id 当前选择的id,商品就是id,网点就是cust_code
 * @param {number} type 1:收藏  2:加入购物车  3:网点  4.论坛
 * @param {number} from 1:pc端  2:移动端  3:车机端
 * @return {Promise}
 * */
export const pushIntention = function (id, type, from) {
  return new Promise((resolve, reject) => {
    axios.post(MaintainUrl.intention, {code: id, class: type, from: from})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}



