<template>
  <div id="app">
    <!--<div style="padding: 10px 0;text-align: center;background: red; color: #fff;">系统维护中，暂时无法注册新用户或参加活动，预计恢复时间2017.12.11 14:00</div>-->
    <router-view></router-view>
    <nav-bar></nav-bar>
    <div class="yuyue" v-on:click="goTo('http://oa.sih.cq.cn:8081/workflow/buyindex/index_zkh.html', true)">
      我要购车
    </div>
  </div>
</template>

<script>
import NavBar from './views/footer-bar'
export default {
  name: 'app',
  data () {
    return {
      active: '商城'
    }
  },
  methods: {
    goTo (path) {
      window.location.href = path
    }
  },
  components: {
    NavBar
  }
}
</script>

<style lang="less">
  .yuyue{
    position: fixed;
    right: 0;
    bottom: 20%;
    background: #2d8cf0;
    width: 60px;
    height: 60px;
    text-align: center;
    font-size: 12px;
    line-height: 60px;
    color: #fff;
  }
</style>



