<template>
  <div>
    <div v-if="!detailBtnState" v-show="!createdOrderBtnState">
      <mt-tabbar v-model="selected" :fixed="isFixed">
        <mt-tab-item id="首页" v-on:click.native="goTo('/mall')">
          <i slot="icon" class="fa fa-home" aria-hidden="true"></i>
          首页
        </mt-tab-item>
        <mt-tab-item id="用车" v-on:click.native="goTo('/mall/goods/all')">
          <i slot="icon" class="fa fa-wrench" aria-hidden="true"></i>
          商城
        </mt-tab-item>
        <mt-tab-item id="论坛" v-on:click.native="goTo('/forum')">
          <i slot="icon" class="fa fa-leaf" aria-hidden="true"></i>
          论坛
        </mt-tab-item>
        <mt-tab-item id="我的" v-on:click.native="goTo('/home')">
          <i slot="icon" class="fa fa-user" aria-hidden="true"></i>
          我的
        </mt-tab-item>
      </mt-tabbar>
    </div>
    <div v-else v-show="!createdOrderBtnState">
      <mt-tabbar :fixed="isFixed">
        <mt-tab-item id="商城" v-on:click.native="goTo('/mall/goods/all')" style="flex: .5">
          <i slot="icon" class="fa fa-home" aria-hidden="true"></i>
          商城
        </mt-tab-item>
        <!--<mt-tab-item id="收藏" v-on:click.native="collect" style="flex: .5;">-->
          <!--<i slot="icon"  class="fa fa-star" aria-hidden="true"></i>-->
          <!--收藏-->
        <!--</mt-tab-item>-->
        <mt-tab-item id="加入购物车" @click.native="addToCart" :class="{'cant-click': addCartBtnState}" disabled style="flex: .5; background-color: rgb(255, 153, 1); color: #fff;">
          <i slot="icon" class="fa fa-cart-plus" aria-hidden="true"></i>
          加入购物车({{cartCount}})
        </mt-tab-item>
        <mt-tab-item id="购物车" v-on:click.native="buyNow" :class="{'cant-click': addCartBtnState}" disabled style="flex: 1; background-color: rgb(254, 86, 10); color: #fff;">
          <i slot="icon" class="fa fa-shopping-cart" aria-hidden="true"></i>
          立即购买
        </mt-tab-item>
      </mt-tabbar>
    </div>
    <div class="footer-btn" v-show="createdOrderBtnState">
      <div class="info">
        共<span style="color: #f50">{{order.count}}</span>件，总金额 <span class="dollar">￥</span><span class="main-price">{{order.price}}</span>
      </div>
      <div class="btn" @click="submitOrder">
        <a href="javascript:void(0)">提交订单</a>
      </div>
    </div>
  </div>
</template>

<script>
import * as types from '../store/mutation-types'
import bus from '../assets/js/bus'
export default {
  name: 'Footer-Bar',
  data () {
    return {
      selected: '商城',
      isFixed: true,
      isDetail: false
    }
  },
  created () {
  },
  watch: {
  },
  computed: {
    detailBtnState () {
      return this.$store.getters.getMallBtnState
    },
    addCartBtnState () {
      return this.$store.getters.getAddBtnState
    },
    cartCount () {
      return this.$store.getters.getMallCartCount
    },
    createdOrderBtnState () {
      return this.$store.getters.getCreateOrderBtnState
    },
    order () {
      return {
        count: this.$store.getters.getGoodsCount,
        price: this.$store.getters.getTotalPrice
      }
    }
  },
  beforeDestory () {
    bus.$off('addCart')
    bus.$off('clickSubmitOrder')
    bus.$off('addCardError')
  },
  methods: {
    goTo (path, type) {
      if (type) {
        return window.open(path)
      } else {
        this.$router.replace(path)
      }
    },
    addToCart () {
      if (this.$store.getters.getAddBtnState) {
        bus.$emit('addCardError')
      } else {
        this.$store.commit(types.MODIFY_MALL_CART_COUNT, this.cartCount + 1)
        bus.$emit('addCart')
      }
    },
    buyNow () {
      if (this.$store.getters.getAddBtnState) {
        bus.$emit('buyNowError')
      } else {
        bus.$emit('buyNow')
      }
    },
    submitOrder () {
      bus.$emit('clickSubmitOrder')
    }
  }
}
</script>

<style lang="less" scoped>
  .cant-click{
    background-color: #ffdab9 !important;
  }
  .footer-btn{
    right: 0;
    bottom: 0;
    left: 0;
    position: fixed;
    z-index: 1;
    background-color: #fafafa;
    margin-top: 9px;
    box-shadow: 0 -1px 0 0 rgba(0,0,0,.1), 0 -0.5px 0.5px 0 rgba(0,0,0,.2);
    display: flex;
    align-items: center;
    .info{
      text-align: right;
      margin-right: 9px;
      flex: 1;
      .dollar {
        color: #f50;
        font-size: 12px;
      }
      .main-price {
        font-size: 16px;
        color: #f50;
      }
    }
    .btn{
      background-color: #f50;
      color: #fff;
      padding: 1.1rem 1.4rem;
      font-size: 16px;
      align-items: center;
      a {
        color: #fff;
      }
    }
  }
</style>



