/* eslint-disable */
export const setPageTitle = function (title) {
  document.title = title
}

/**
 * 添加localStorage
 * @param {string} key
 * @param {any} value
 * */
export const setLocalStorage = function (key, value) {
  localStorage.setItem(key, value)
}

/**
 * 获取localStorage
 * @param {string} key
 * */
export const getLocalStorage = function (key) {
  return localStorage.getItem(key)
}

/**
 * 删除localStorage
 * @param {string} key
 * */
export const removeLocalStorage = function (key) {
  localStorage.removeItem(key)
}

/**
 * 判断设备是否为微信
 * */
export const isWechat = function () {
  return (window.navigator.userAgent.toLowerCase().match(/MicroMessenger/i) == 'micromessenger')
}

export const setHeight = function (wrap, cont) {
  var sHeight = document.getElementById(wrap).offsetHeight;
  var eHeight = document.getElementById(cont).offsetHeight;
  return sHeight - eHeight;
}
/**
 * 判断是否为移动端
 * */
export const isMobile = function () {
  let _u = navigator.userAgent
  return !!_u.match(/AppleWebKit.*Mobile.*/)
}

export const getQueryString = function (name) {
  var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
  var r = window.location.search.substr(1).match(reg);
  if (r != null) {
    return unescape(r[2]);
  }
  return null;
}

/**
 * 商品详情页配置选择器
 * @param {Array} details 商品配置表
 * @return {Array} detailOption
 * */
export const Menu = {
  details: {},
  unSelect: [],
  init: function (details) {
    this.details = details
    if (details.length <= 1) return
    let detailOption = []
    let detailAssist = []
    // let priceMin = null
    // let priceMax = null
    for (let i in details) {
      // if (!priceMin || priceMin > details[i].price) priceMin = details[i].price
      // if (!priceMax || priceMax < details[i].price) priceMax = details[i].price
      let d = details[i].options
      for (let j in d) {
        let dd = d[j]
        let configID = dd['config_id']
        let detailID = dd['detail_id']
        let configName = dd['config_name_cn']
        let configLength = detailAssist[configID]
        let configDescripton = dd['config_descripton']
        let CD = dd['config_id']
        if (!configLength) {
          detailAssist[configID] = detailOption.length + 1
          configLength = detailAssist[configID]
          detailOption.push({
            detail_id: detailID,
            config_id: configID,
            config_name: configName,
            config_options: {name: [], detail_id: [], config_id: []},
            detail_option_id: 0,
            config_descripton: configDescripton
          })
        }
        configLength--
        // console.log(dd)
        let optionNameCn = dd['option_name_cn']
        // let configID = CD['config']
        detailOption[configLength]['config_options'].name.push(optionNameCn)
        detailOption[configLength]['config_options'].detail_id.push(detailID)
        detailOption[configLength]['config_options'].config_id.push(CD)
      }
    }
    for (let i in detailOption) {
      let options = []
      let opt = detailOption[i]['config_options'].name
      let IDS = detailOption[i]['config_options'].detail_id
      let configID = detailOption[i]['config_options'].config_id
      for (let j in opt) {
        let optionNameCn = opt[j]
        options.push({option_name_cn: optionNameCn, detail_id: IDS[j], config_id: configID[j], isActive: true})
      }
      let hash = {}
      options = options.reduce((item, next) => {
        hash[next.option_name_cn] ? '' : hash[next.option_name_cn] = true && item.push(next)
        return item
      }, [])
      detailOption[i]['config_options'] = options
    }
    // console.log(detailOption)
    return {
      detailOption
    }
  },
  check: function (selectDetail) {
    let details = this.details
    if (details.length <= 1) return
    // console.log(selectDetail);
    let newDetail = [] // 外层
    for (let i in details) {
      let isAc = true;
      let d = details[i].options
      let index = 0;
      //判断当前状态
      for (let z of d) {
        for (let s of selectDetail){
          if(z.config_id === s.id){
            if(z.option_name_cn ===s.name){
              index++; //相等了多少次
            }
          }
        }
        if(index == selectDetail.length){
          isAc = true
        } else {
          isAc = false
        }
      }
      // console.log(isAc);
      for (let j of d) {
        let inDetail = false
        for (let f = 0; f<newDetail.length; f++) { //遍历新数组
          let ff = newDetail[f];
          if (ff.config_id === j.config_id) { // 同一组
            inDetail = true
            let isSame = false;

            for (let nn=0;nn<ff.config_options.length;nn++){ //遍历内部字符串名称
              let n = ff.config_options[nn];
              if (n.option_name_cn == j.option_name_cn) { //名称相同，更新
                isSame = true
                if(n.isActive || isAc){  //只要是选中，都选中
                  ff.config_options[nn] = {option_name_cn:j.option_name_cn, detail_id: j.detail_id,config_id: j.config_id, isActive:true}
                }
              }
            }
            if(!isSame){ //不相同在后面追加
              newDetail[f].config_options.push({option_name_cn:j.option_name_cn, detail_id: j.detail_id,config_id: j.config_id, isActive:isAc})
            }
          }
        }
        if (!inDetail) { //不存在添加
          newDetail.push({
            config_options:[{option_name_cn:j.option_name_cn, detail_id: j.detail_id,config_id: j.config_id, isActive:isAc}],
            config_id: j.config_id,
            config_name: j.config_name_cn
          })
        }
      }
    }
    // console.log(newDetail);
    return newDetail
  }
}



