import * as types from '../mutation-types'
const state = {
  addBtnState: true,
  mallBtnState: false,
  mallCartCount: 0,
  createOrderBtnState: false,
  goodsCount: 0,
  totalPrice: 0,
  useCardList: [],
  selectedOptions: [],
  useCardObj: {}
}

const getters = {
  getAddBtnState: state => state.addBtnState,
  getMallBtnState: state => state.mallBtnState,
  getMallCartCount: state => state.mallCartCount,
  getCreateOrderBtnState: state => state.createOrderBtnState,
  getGoodsCount: state => state.goodsCount,
  getTotalPrice: state => state.totalPrice,
  getUseCardList: state => state.useCardList,
  getUseCardObject: state => state.useCardObj,
  allSelectedOptions: state => state.selectedOptions
}
const mutations = {
  [types.MODIFY_ADD_BTN_STATE] (state, val) {
    state.addBtnState = val
  },
  [types.MODIFY_MALL_DETAIL_BTN_STATE] (state, val) {
    state.mallBtnState = val
  },
  [types.MODIFY_MALL_CART_COUNT] (state, num) {
    state.mallCartCount = num
  },
  [types.MODIFY_MALL_CREATED_ORDER] (state, val) {
    state.createOrderBtnState = val
  },
  [types.SET_ORDER_INFO] (state, val) {
    state.goodsCount = val.count
    state.totalPrice = val.price
  },
  [types.SET_ORDER_INFO_PRICE] (state, price) {
    state.totalPrice = price
  },
  [types.ADD_USE_CARD] (state, obj) {
    state.useCardList.push(obj)
  },
  [types.REMOVE_USE_CARD] (state, index) {
    state.useCardList.splice(index, 1)
  },
  [types.SET_USE_CARD_OBJECT] (state, obj) {
    state.useCardObj[obj.key] = obj.val
  },
  // 商品详情里选中的option
  [types.ADD_SELECTED_OPTIONS] (state, opt) {
    let obj = {}
    obj['id'] = opt.config_id
    obj['name'] = opt.option_name_cn
    state.selectedOptions.push(obj)
  },
  // 移出取消选中的option
  [types.REMOVE_SELECTED_OPTIONS] (state, index) {
    state.selectedOptions.splice(index, 1)
  }
}

export default {
  state,
  getters,
  mutations
}



