import * as types from '../mutation-types'
import {getLocalStorage, setLocalStorage} from '../../lib/util'

const state = {
  dealerLoginSate: getLocalStorage('dealerState') || false,
  dealerInfo: JSON.parse(getLocalStorage('dealerInfo')) || {}
}

const getters = {
  getDealerLoginState: state => state.dealerLoginSate,
  getDealerInfo: state => state.dealerInfo
}

const mutations = {
  [types.CHANGE_DEALER_LOGIN_STATE] (state, val) {
    state.dealerLoginSate = val
    setLocalStorage('dealerState', val)
  },
  [types.SET_DEALER_LOGIN_INFO] (state, val) {
    state.dealerInfo = val
    setLocalStorage('dealerInfo', val)
  }
}

export default {
  state,
  getters,
  mutations
}



