/**
 * 获取商品价格
 * @param {String} detail 参数:detail:{"196149":"1","196150":"3"}
 * */
export const mallOrderGetPrice = function (detail, coupon) {
  return new Promise((resolve, reject) => {
    axios.post(MallUrl.orderGetPrice, {detail: detail, coupon})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * 创建订单
 * @param {String} detail
 * @param {int} addressID
 * @param {String} content
 * */
export const orderCreate = function (detail, addressID, content, coupon) {
  return new Promise((resolve, reject) => {
    axios.post(MallUrl.orderCreate, {detail: detail, address_id: addressID, content: content, coupon})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * 获取订单信息
 * */
export const getOrderInfo = function (orderID) {
  return new Promise((resolve, reject) => {
    axios.post(MallUrl.getorderinfo, {order_no: orderID})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * 订单支付,微信支付
 * */
export const payTenpayQrcode = function (body, orderID) {
  return new Promise((resolve, reject) => {
    axios.post(MallUrl.payTenPayQrcode, {body: body, order_no: orderID})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * @desc 调用微信支付
 * @param {string} orderID
 * @param {string} body
 * */
export const tenPayInWechat = function (orderID, body) {
  return new Promise((resolve, reject) => {
    axios.post(MallUrl.tenpayJsApi, {order_no: orderID, body: body})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * 轮询微信支付
 * */
export const payQuery = function (orderID) {
  return new Promise((resolve, reject) => {
    axios.post(MallUrl.payQuery, {order_no: orderID})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

export const getIndexSwpieList = () => {
  return new Promise((resolve, reject) => {
    axios.get(IndexUrl.indexSwiper)
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * 商品详情页
 * @param {int} mallID 商品ID
 * @param {Array} options 选中的option
 * */
export const getOptions = function (mallID, options) {
  return new Promise((resolve, reject) => {
    axios.post(MallUrl.options, {mall_id: mallID, option_names: options})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}



