// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import MintUI from 'mint-ui'
import router from './router'
import store from './store'
import * as types from './store/mutation-types'
import axios from 'axios'
import Cookie from 'js-cookie'
import VueLazyload from 'vue-lazyload'

import {initWechatSDK, wechatLogin} from './assets/js/wechat'
import {getMallCartCount} from './api/mall'
import {getUserInfo} from './api/login'
import {getDealerInfo} from './api/maintain'
import {isWechat, setPageTitle, isMobile} from './lib/util'

import 'mint-ui/lib/style.css'
import './assets/less/style.less'

Vue.use(MintUI)
Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: '/static/img/bg.png',
  loading: '/static/img/rolling.gif',
  attempt: 1
})
Vue.prototype.$http = axios
Vue.config.productionTip = false
axios.defaults.withCredentials = true
let showMessage = true

router.beforeEach((to, from, next) => {
  setPageTitle(to.meta.title)
  if (to.name === 'Mall-Detail') {
    store.commit(types.MODIFY_MALL_DETAIL_BTN_STATE, true)
    next()
  } else if (to.name === 'Mall-Confirm') {
    store.commit(types.MODIFY_MALL_CREATED_ORDER, true)
    next()
  } else {
    store.commit(types.MODIFY_MALL_DETAIL_BTN_STATE, false)
    store.commit(types.MODIFY_MALL_CREATED_ORDER, false)
    next()
  }
  if (!isMobile()) {
    if (showMessage) {
      MintUI.MessageBox.confirm('是否自动跳转到重卡汇电脑版?').then(action => {
        if (action) {
          window.location.href = 'http://www.truckgogo.com'
        }
      }).catch(() => {
        showMessage = false
      })
    }
  } else {
    next()
  }
  if (window.location.host === 'm.hongyanche.com') {
    let a = window.location.href.replace(/m.hongyanche.com/, 'm.truckgogo.com')
    window.location.href = a
  }
  getUserInfo()
    .then(data => {
      // 如果用户信息请求成功
      if (data.state) {
        store.dispatch('userLoginAction', data.data.info)
        if (!data.data.info.password || !data.data.info.tel) {
          if (to.meta && to.meta.role) {
            MintUI.Toast('请先绑定您的重卡汇账号')
            router.push('/user/select')
          }
        }
        if (to.meta.role && to.meta.role.find(val => val === 'dealer')) {
          getDealerInfo()
            .then(data => {
              if (data.state) {
                store.commit(types.SET_DEALER_LOGIN_INFO, JSON.stringify(data.data))
                store.commit(types.CHANGE_DEALER_LOGIN_STATE, true)
                next()
              } else {
                next({path: '/login?type=dealer'})
              }
            })
            .catch(() => {
              next({path: '/login?type=dealer'})
            })
        }
        getMallCartCount().then(data => {
          store.commit(types.MODIFY_MALL_CART_COUNT, data.data.amount)
        })
        next()
      } else {
        if (isWechat()) {
          initWechatSDK()
          wechatLogin()
        } else {
          if (to.meta && to.meta.role) {
            if (to.meta.role.find((r) => r === 'login')) {
              next({path: '/login'})
            } else if (to.meta.role.find((r) => r === 'dealer')) {
              // 如果是经销商
              getDealerInfo()
                .then(data => {
                  if (data.state) {
                    store.commit(types.SET_DEALER_LOGIN_INFO, JSON.stringify(data.data))
                    store.commit(types.CHANGE_DEALER_LOGIN_STATE, true)
                    next()
                  } else {
                    next({path: '/login?type=dealer'})
                  }
                })
                .catch(err => {
                  console.log(err)
                  MintUI.Toast('网络繁忙，请重试')
                  next({path: '/'})
                })
            } else {
              next()
            }
          } else {
            next()
          }
        }
      }
    })
    .catch(err => {
      console.log(err)
      MintUI.Toast('网络繁忙，请重试')
      next({path: '/'})
    })
  next()
})

window.Cookie = Cookie

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  template: '<App/>',
  components: {App}
})



