import axios from 'axios'
import {Login, LoginUrl} from '../config/urls'
import {getQueryString} from '../lib/util'

export const getUserInfo = function () {
  if (getQueryString('t')) {
    return new Promise((resolve, reject) => {
      axios.get(Login.getInfo, {
        params: {
          partner_code: getQueryString('t'),
          from: 1
        }
      })
        .then(data => resolve(data.data))
        .catch(err => reject(err))
    })
  }
  return new Promise((resolve, reject) => {
    axios.get(Login.getInfo)
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * 用户登陆
 * @param {string} uname 用户名
 * @param {string} pw 密码
 * @param {string} vc 验证码
 * @param {string} auto 自动登陆
 * @return {Promise}
 * */
export const loginForPassword = function (uname, pw, vc, auto, passwordx) {
  return new Promise((resolve, reject) => {
    axios.post(LoginUrl.login, {
      account: uname,
      password: pw,
      vcode: vc,
      passwordx: passwordx,
      auto: auto
    }).then(data => {
      if (data.data.errormsg === 'vcode error') {
        reject('vcode')
      } else {
        resolve(data.data)
      }
    }).catch(err => reject(err))
  })
}

// 退出登陆
export const loginOut = () => {
  return new Promise((resolve, reject) => {
    axios.get(LoginUrl.loginOut).then(data => resolve(data)).catch(err => reject(err))
  })
}

/**
 * 发送短信验证码
 * @param {int} tel 手机号码
 * @param {String} vcode 图形验证码
 * @return {Promise}
 * */
export const sendSms = (tel, vcode) => {
  return new Promise((resolve, reject) => {
    axios.post(LoginUrl.verification, {tel: tel, vcode: vcode})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * 手机注册
 * @param {int} tel 手机号
 * @param {String} code 验证码
 * @param {int} introduce 推荐人账号
 * @param {String} password 密码
 * @param {String} passwordx 确认密码
 * @return {Promise}
 * */
export const registerCheck = (tel, code, introduce, password, passwordx) => {
  return new Promise((resolve, reject) => {
    axios.post(LoginUrl.register, {tel: tel, code: code, introduce: introduce, password: password, passwordx: passwordx})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

/**
 * 重置密码
 * @param {int} tel 手机号
 * @param {String} code 验证码
 * @param {String} password 密码
 * @param {String} passwordx 确认密码
 * @return {Promise}
 * */
export const resetPassword = (tel, vcode, code, password, passwordx) => {
  return new Promise((resolve, reject) => {
    axios.post(LoginUrl.passwordReset, {tel: tel, vcode: vcode, code: code, password: password, passwordx: passwordx})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}

export const bind = function (tel, pd) {
  return new Promise((resolve, reject) => {
    axios.post(LoginUrl.bind, {tel: tel, password: pd})
      .then(data => resolve(data.data))
      .catch(err => reject(err))
  })
}



