import Vue from 'vue'
import Vuex from 'vuex'

import User from './model/user'
import Forum from './model/forum'
import Mall from './model/mall'
import Dealer from './model/dealer'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    User,
    Forum,
    Mall,
    Dealer
  }
})



