import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/mall'
    },
    {
      path: '/mall',
      name: 'Mall',
      meta: {
        title: '商城 - 重卡汇'
      },
      component: (resolve) => require(['../views/mall/mall'], resolve)
    },
    {
      path: '/mall/goods/:id',
      name: 'Mall-Goods',
      meta: {
        title: '重卡汇商城'
      },
      component: resolve => require(['../views/mall/goods/goods'], resolve)
    },
    {
      path: '/mall/item/:id',
      name: 'Mall-Detail',
      meta: {
        title: '商品详情 - 重卡汇'
      },
      component: resolve => require(['../views/mall/detail/detail'], resolve)
    },
    {
      path: '/mall/cart',
      name: 'Mall-Cart',
      meta: {
        title: '我的购物车 - 重卡汇',
        role: ['login']
      },
      component: resolve => require(['../views/mall/cart/cart'], resolve)
    },
    {
      path: '/mall/confirm',
      name: 'Mall-Confirm',
      meta: {
        title: '订单确认',
        role: ['login']
      },
      component: resolve => require(['../views/mall/pay/confirm'], resolve)
    },
    {
      path: '/mall/pay',
      name: 'Mall-Pay',
      meta: {
        title: '结算中心 - 重卡汇',
        role: ['login']
      },
      component: resolve => require(['../views/mall/pay/pay'], resolve)
    },
    {
      path: '/mall/aftersales/:id',
      name: 'Mall-afterSales',
      meta: {
        title: '申请售后 - 重卡汇',
        role: ['login']
      },
      component: resolve => require(['../views/mall/aftersales/aftersales'], resolve)
    },
    {
      path: '/maintain',
      name: 'Maintain',
      meta: {
        title: '服务 - 重卡汇'
      },
      component: (resolve) => require(['../views/maintain/maintain'], resolve)
    },
    {
      path: '/maintain/fuel',
      name: 'Maintain-Fuel',
      meta: {
        title: '油耗对比',
        role: ['login']
      },
      component: resolve => require(['../views/maintain/fuel/fuel'], resolve)
    },
    {
      path: '/forum',
      name: 'Forum',
      meta: {
        title: '论坛 - 重卡汇'
      },
      component: (resolve) => require(['../views/forum/forum'], resolve)
    },
    {
      path: '/forum/list/:id',
      name: 'Forum-Label',
      meta: {
        title: '论坛 - 重卡汇'
      },
      component: (resolve) => require(['../views/forum/label/label'], resolve)
    },
    {
      path: '/forum/posts/:id',
      name: 'Forum-Posts',
      meta: {
        title: '帖子详情页 - 重卡汇'
      },
      component: (resolve) => require(['../views/forum/posts/posts'], resolve)
    },
    {
      path: '/forum/create/:id',
      name: 'Forum-Create',
      meta: {
        title: '发表新帖 - 重卡汇',
        role: ['login']
      },
      component: (resolve) => require(['../views/forum/create/create'], resolve)
    },
    {
      path: '/forum/home/:id',
      name: 'Forum-Home',
      meta: {
        title: '论坛个人中心 - 重卡汇'
      },
      component: (resolve) => require(['../views/forum/home/home'], resolve)
    },
    {
      path: '/home',
      name: 'Home',
      meta: {
        title: '个人中心 - 重卡汇',
        role: ['login']
      },
      component: (resolve) => require(['../views/home/home'], resolve)
    },
    {
      path: '/tasks',
      name: 'Tasks',
      meta: {
        title: '任务中心 - 重卡汇',
        role: ['login']
      },
      component: resolve => require(['../views/home/tasks/tasks'], resolve)
    },
    {
      path: '/home/card',
      name: 'Card',
      meta: {
        title: '我的卡券 - 重卡汇',
        role: ['login']
      },
      component: resolve => require(['../views/home/card/card'], resolve)
    },
    {
      path: '/home/user',
      name: 'Home-User',
      meta: {
        title: '个人设置',
        role: ['login']
      },
      component: resolve => require(['../views/home/user/user'], resolve)
    },
    {
      path: '/user/select',
      name: 'User-Select',
      meta: { title: '重卡汇' },
      component: resolve => require(['../views/home/user/user-select'], resolve)
    },
    {
      path: '/home/bind',
      name: 'Home-Bind',
      meta: {
        title: '绑定重卡汇账号'
      },
      component: resolve => require(['../views/home/user/user-bind.vue'], resolve)
    },
    {
      path: '/home/account/set',
      name: 'Home-Account-Set',
      meta: {
        title: '绑定手机'
      },
      component: resolve => require(['../views/home/user/user-set'], resolve)
    },
    {
      path: '/home/set',
      name: 'Home-Set',
      meta: {
        title: '个人设置'
      },
      component: resolve => require(['../views/home/user/user-set'], resolve)
    },
    {
      path: '/merchant/apply',
      name: 'Merchant-Apply',
      meta: {
        title: '商户入驻'
      },
      component: resolve => require(['../views/merchant/apply-merchant'], resolve)
    },
    {
      path: '/dealer',
      name: 'Dealer',
      meta: {
        title: '经销商中心',
        role: ['dealer']
      },
      component: resolve => require(['../views/dealer/dealer'], resolve)
    },
    {
      path: '/dealer/dealer_list',
      name: 'Dealer-List',
      meta: {
        title: '经销商列表',
        role: ['dealer']
      },
      component: resolve => require(['../views/dealer/dealer-list'], resolve)
    },
    {
      path: '/dealer/verify',
      name: 'Dealer-Verify',
      meta: {
        title: '核销卡券',
        role: ['dealer']
      },
      component: resolve => require(['../views/dealer/dealer-verify.vue'], resolve)
    },
    {
      path: '/dealer/bind_tel',
      name: 'Dealer-Bind-Tel',
      meta: {
        title: '绑定手机',
        role: ['dealer']
      },
      component: resolve => require(['../views/dealer/dealer-bind-tel.vue'], resolve)
    },
    {
      path: '/dealer/modify_pwd',
      name: 'Dealer-Modify-PWD',
      meta: {
        title: '修改密码',
        role: ['dealer']
      },
      component: resolve => require(['../views/dealer/dealer-password.vue'], resolve)
    },
    {
      path: '/dealer/verify/list',
      name: 'Dealer-Verify-List',
      meta: {
        title: '已核销列表',
        role: ['dealer']
      },
      component: resolve => require(['../views/dealer/dealer-verify-list'], resolve)
    },
    {
      path: '/dealer/create_account',
      name: 'Dealer-Create-Account',
      meta: {
        title: '新增账号',
        role: ['dealer']
      },
      component: resolve => require(['../views/dealer/dealer-create-account.vue'], resolve)
    },
    {
      path: '/dealer/btob',
      name: 'Dealer-BToB',
      meta: {
        title: '经销商对经销商',
        role: ['dealer']
      },
      component: resolve => require(['../views/dealer/dealer-btob.vue'], resolve)
    },
    {
      path: '/dealer/download',
      name: 'Dealer-Download',
      meta: {
        title: '报表下载',
        role: ['dealer']
      },
      component: resolve => require(['../views/dealer/dealer-time.vue'], resolve)
    },
    {
      path: '/dealer/data',
      name: 'Dealer-Name',
      meta: {
        title: '设置数据走向',
        role: ['dealer']
      },
      component: resolve => require(['../views/dealer/dealer-data.vue'], resolve)
    },
    {
      path: '/dealer/account_list',
      name: 'Dealer-Account-List',
      meta: {
        title: 'SIH子用户列表',
        role: ['dealer']
      },
      component: resolve => require(['../views/dealer/dealer-account-list.vue'], resolve)
    },
    {
      path: '/dealer/message',
      name: 'Dealer-Message',
      meta: {
        title: '消息中心',
        role: ['dealer']
      },
      component: resolve => require(['../views/dealer/dealer-message.vue'], resolve)
    },
    {
      path: '/dealer/forget',
      name: 'Dealer-Forget',
      meta: {
        title: '忘记密码 - 经销商'
      },
      component: resolve => require(['../views/login/dealer-forget-password.vue'], resolve)
    },
    {
      path: '/message',
      name: 'Message-Page',
      meta: {
        title: '我的消息 - 重卡汇',
        role: ['login']
      },
      component: (resolve) => require(['../views/home/message/message'], resolve)
    },
    {
      path: '/vehicle',
      name: 'Vehicle',
      meta: {
        title: '我的车辆 - 重卡汇',
        role: ['login']
      },
      component: (resolve) => require(['../views/home/vehicle/vehicle'], resolve)
    },
    {
      path: '/order',
      name: 'Order',
      meta: {
        title: '我的订单 - 重卡汇',
        role: ['login']
      },
      component: (resolve) => require(['../views/home/order/order'], resolve)
    },
    {
      path: '/collect',
      name: 'Collect',
      meta: {
        title: '我的收藏 - 重卡汇',
        role: ['login']
      },
      component: (resolve) => require(['../views/home/collect/collect'], resolve)
    },
    {
      path: '/carte',
      name: 'Carte',
      meta: {
        title: '我的名片 - 重卡汇',
        role: ['login']
      },
      component: (resolve) => require(['../views/home/carte/carte'], resolve)
    },
    {
      path: '/address',
      name: 'Address',
      meta: {
        title: '我的收货地址 - 重卡汇',
        role: ['login']
      },
      component: (resolve) => require(['../views/home/address/address'], resolve)
    },
    {
      path: '/network',
      name: 'Network',
      meta: {
        title: '全国网点查询'
      },
      component: (resolve) => require(['../views/maintain/network/netwrok'], resolve)
    },
    {
      path: '/track',
      name: 'Track',
      meta: {
        title: '行车轨迹查询 - 重卡汇',
        role: ['login']
      },
      component: (resolve) => require(['../views/maintain/track/track'], resolve)
    },
    {
      path: '/track/panel/:id',
      name: 'Track-Panel',
      meta: {
        title: '车辆详细信息 - 重卡汇',
        role: ['login']
      },
      component: resolve => require(['../views/maintain/track/panel/panel'], resolve)
    },
    {
      path: '/track/panel/info/:id',
      name: 'Car-Info',
      meta: {
        title: '车辆信息 - 重卡汇',
        role: ['login']
      },
      component: resolve => require(['../views/maintain/track/panel/carinfo'], resolve)
    },
    {
      path: '/track/panel/report/:id',
      name: 'Track-Report',
      meta: {
        title: '数据报表 - 重卡汇',
        role: ['login']
      },
      component: resolve => require(['../views/maintain/track/panel/report'], resolve)
    },
    {
      path: '/track/panel/map/:id',
      name: 'Track-Map',
      meta: {
        title: '行车轨迹',
        role: ['login']
      },
      component: resolve => require(['../views/maintain/track/panel/map'], resolve)
    },
    {
      path: '/track/panel/figure/:id',
      name: 'Track-Figure',
      meta: {
        title: '数据可视化 - 重卡汇',
        role: ['login']
      },
      component: resolve => require(['../views/maintain/track/panel/figure'], resolve)
    },
    {
      path: '/login',
      meta: {
        title: '登陆 - 重卡汇'
      },
      name: 'Login',
      component: (resolve) => require(['../views/login/login'], resolve)
    },
    {
      path: '/login/forget',
      meta: {
        title: '忘记密码 - 重卡汇'
      },
      name: 'Forget',
      component: (resolve) => require(['../views/login/forget-password'], resolve)
    },
    {
      path: '/integral',
      meta: {
        title: '积分规则'
      },
      name: 'Integral',
      component: resolve => require(['../views/home/user/integral'], resolve)
    },
    {
      path: '/download',
      meta: {
        title: '下载中心 - 重卡汇'
      },
      component: resolve => require(['../views/download/download'], resolve)
    },
    {
      path: '/shop/:id',
      meta: {
        title: '店铺 - 重卡汇'
      },
      component: resolve => require(['../views/shop/shop'], resolve)
    },
    {
      path: '/shop/:id/search',
      meta: {
        title: '店铺 - 重卡汇'
      },
      component: resolve => require(['../views/shop/search/search'], resolve)
    }
  ]
})



